import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
import { ProfileProvider } from '../../providers/profile/profile';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public eventList: Array<any>;
  constructor(public navCtrl: NavController, public eventProvider:
    EventProvider, public profileProvider: ProfileProvider) { }


  goToProfile(): void {
    this.navCtrl.push('ProfilePage')
  }
  goToCreate(): void {
    this.navCtrl.push('EventCreatePage')
  }

/*   goToList(): void {
    this.navCtrl.push('EventListPage')
  } */
  goToTermos(): void {
    this.navCtrl.push('TermosPage')
  }
  goToMeusAnuncios(): void {
    this.navCtrl.push('MeusanunciosPage')
  }

  goToPerfilPublico(): void {
    this.navCtrl.push('PerfilPublicoPage')
  }



    ionViewDidLoad() {
      this.eventProvider.getEventList().on('value', eventListSnapshot => {
      this.eventList = [];
      eventListSnapshot.forEach(snap => {
        this.eventList.push({
          id: snap.key,
          Nome: snap.val().Nome,
          Preco: snap.val().Preco,
          Data_inicial: snap.val().Data_inicial,
          Data_final: snap.val().Data_final,
          ID_Dono: snap.val().ID_Dono,          
        });
        return false;
      });
    });
  }
  
  goToEventDetail(eventId):void {
    this.navCtrl.push('AnuncioDetalhesPage', { 'eventId': eventId });
    }






}



