import { Injectable } from '@angular/core';
//import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import firebase from 'firebase';

@Injectable()
export class ProfileProvider {
  public userProfile: firebase.database.Reference;
  public currentUser: firebase.User;
  constructor() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.currentUser = user;
        this.userProfile = firebase.database().ref(`/userProfile/${user.uid}`);
      }
    });
  }

  getUserProfile(): firebase.database.Reference {
    return this.userProfile;
  }

  getNome(): firebase.database.Reference {
    return this.userProfile.key.valueOf.arguments.Nome;
  }

  updateName(Nome: string, Sobrenome: string): firebase.Promise<any> {
    return this.userProfile.update({ Nome, Sobrenome });
  }
  
  updateNiver(Nascimento: string): firebase.Promise<any> {
    return this.userProfile.update({ Nascimento });
  }


  updateCPF(CPF: string): firebase.Promise<any> {
    return this.userProfile.update({ CPF });
  }

  updateRG(RG: string): firebase.Promise<any> {
    return this.userProfile.update({ RG });
  }

  updateTEL1(Tel1: string): firebase.Promise<any> {
    return this.userProfile.update({ Tel1 });
  }

  updateTEL2(Tel2: string): firebase.Promise<any> {
    return this.userProfile.update({ Tel2 });
  }

  pushComent(coment: string, nota: number):  firebase.Promise<any> {
    return this.userProfile.update({ coment, nota });
  }

  updateEmail(newEmail: string, password: string): firebase.Promise<any> {
    const credential = firebase.auth.EmailAuthProvider
      .credential(this.currentUser.email, password);
    return this.currentUser.reauthenticateWithCredential(credential)
      .then(user => {
        this.currentUser.updateEmail(newEmail).then(user => {
          this.userProfile.update({ email: newEmail });
        });
      }).catch(error => {
        console.error(error);
      });
  }


  updatePassword(newPassword: string, oldPassword: string): firebase.Promise<any> {
    const credential = firebase.auth.EmailAuthProvider
      .credential(this.currentUser.email, oldPassword);
    return this.currentUser.reauthenticateWithCredential(credential)
      .then(user => {
        this.currentUser.updatePassword(newPassword).then(user => {
          console.log("Password Changed");
        });
      }).catch(error => {
        console.error(error);
      });
  }


  criarPerfil(nome: string, sobrenome:string, dataNascimento: string, rg: string, cpf: string, tel1: string, tel2: string): firebase.Promise<any> {
    return this.userProfile.update({
      Nome: nome,
      Sobrenome: sobrenome,
      Nascimento: dataNascimento,
      RG: rg,
      CPF: cpf,
      Tel1: tel1,
      Tel2: tel2
    });
  }
}


