import 'rxjs/add/operator/map';
import { Injectable } from '@angular/core';
import firebase from 'firebase';


@Injectable() 
export class EventProvider {
  public eventListRef: firebase.database.Reference;
  
  constructor() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.eventListRef = firebase.database()
          .ref(`/ListaDeVagas`);
        }
    });
  }

  createEvent(eventName: string, eventEspecificacoes: string, eventPrice: number, eventEndereco: string, eventLargura:number, eventComprimento:number, eventDate: string, eventDate1: string, eventDate2: number): firebase.Promise<any> {
    return this.eventListRef.push({
      Nome: eventName,
      Especificacoes: eventEspecificacoes,
      Preco: eventPrice,
      Endereco: eventEndereco,
      Largura: eventLargura,
      Comprimento: eventComprimento,
      Data_inicial: eventDate,
      Data_final: eventDate1,
      Numero_de_vagas: eventDate2,
      ID_Dono: firebase.auth().currentUser.uid
    });
  }

 updateVagaName(Name: string, eventId: string): firebase.Promise<any> 
  {   
    return this.eventListRef.child(eventId).update({ Nome: Name });
  }
  updateVagaEspecifica(Especifica: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Especificacoes: Especifica });
  }
  updateVagaPreco(Preco: number, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Preco: Preco });
  }
  updateVagaEndereco(Endereco: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Endereco: Endereco });
  }
  updateVagaLargura(Largura: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Largura: Largura });
  }  
  updateVagaComprimento(Comprimento: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Comprimento: Comprimento });
  }  
  updateVagaDataInicial(DataInicial: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Data_inicial: DataInicial });
  }
  updateVagaDataFinal(DataFinal: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Data_final: DataFinal });
  }  
  updateVagaNumeroVagas(Numero_de_vagas: string, eventId: string): firebase.Promise<any> 
  {        
    return this.eventListRef.child(eventId).update({ Numero_de_vagas: Numero_de_vagas });
  }


  getMeusAnuncios(): firebase.database.Query {
    
    var query = firebase.database().ref(`/ListaDeVagas`).orderByChild('ID_Dono').equalTo(firebase.auth().currentUser.uid);
        return query;
      }

  getEventList(): firebase.database.Query {
    
    var query = firebase.database().ref(`/ListaDeVagas`);//.orderByChild('ID_Dono').equalTo(firebase.auth().currentUser.uid);
        return query;
      }
  

  // USANDO PARAMETRO, uso futuro!
  /*
  getEventList2(eventPrice: number): firebase.database.Query {
    
    var query = firebase.database().ref('eventList').orderByChild('price').equalTo(eventPrice);

        return query;
  }
   */ 


  getEventDetail(eventId: string): firebase.database.Reference {
    return this.eventListRef.child(eventId);
  }

  addGuest(guestName: string, eventId: string, eventPrice: number):
    firebase.Promise<any> {
    return this.eventListRef.child(`${eventId}/guestList`).push({ guestName })
  }
}



